import config from "../config";
import { authHeader } from "../helpers";
import { userService } from "../services";

export const categoryService = {
  getCategories,
  createCat
};

function getCategories() {
  const requestOptions = {
    method: "GET",
    headers: authHeader()
  };

  return fetch(`${config.api.URL}/categories`, requestOptions).then(
    handleResponse
  );
}

function createCat() {
  alert(3)
  const requestOptions = {
    method: "POST",
    headers: authHeader(),
    body: JSON.stringify({
      // params: {
        name: "minion",
        image_url:
          "https://cdn4.iconfinder.com/data/icons/famous-characters-add-on-vol-1-flat/48/Famous_Character_-_Add_On_1-05-512.png"
      // }
    })
  };
  alert(JSON.stringify(requestOptions));
  return fetch(`${config.api.URL}/categories`, requestOptions).then(
    handleResponse
  );
}

function handleResponse(response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text);
    if (!response.ok) {
      if (response.status === 401) {
        // auto logout if 401 response returned from api
        userService.logout();
        // location.reload(true);
      }

      const error = (data && data.message) || response.statusText;
      return Promise.reject(error);
    }

    return data;
  });
}
